@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.space{
  height: 10px;
  background-color: white;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}
.dashboard{
  margin: auto;
  text-align: center;
}
.users,
.posts{
  display: -webkit-flex;
  display: flex;
  height: 90vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 3rem;
}

.navbar {
    background-color: #060b26;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
  .nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: #1a83ff;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
  }
  
  span {
    margin-left: 16px;
  }

body {
    font-family: Arial, Helvetica, sans-serif;
    background-color: rgb(215, 226, 166);
    -webkit-align-content: center;
            align-content: center;
  }
  
  .container {
    position: relative;
    border: 3px solid rgb(241, 83, 149);
    border-radius: 5px;
    width: 600px;
    margin: auto;
    background-color: #f06bb4;
    padding: 20px 0 30px 0;
    text-align: center;
  }
  
  input,
  .btn {
    width: 50%;
    padding: 12px;
    border: 2px solid rgb(64, 35, 92);
    border-radius: 4px;
    margin: auto;
    font-size: 17px;
    line-height: 20px;
    text-align: center;
  }
  
  input:focus {
    background-color: rgb(146, 227, 241);
  }
  button[type="submit"] {
    background-color: #4caf50;
    color: white;
    width: 50%;
    padding: 12px;
    border: 2px solid rgb(64, 35, 92);
    border-radius: 4px;
    margin: auto;
    font-size: 17px;
    line-height: 20px;
    text-align: center;
  }
  button[type="submit"]:hover {
    background-color: #45a049;
  }
  h1 {
    margin-left: 30px;
  }
  p {
    text-align: center;
  }
